import React, { createContext, ReactNode, useContext, useState } from "react";

// Define user roles to match server enum
export enum UserRole {
  User = 0, // previously "member"
  Admin = 1, // previously "admin"
  Staff = 2, // previously "staff"
}

// Define user interface to match your backend
export interface User {
  _id: string;
  name: string;
  email: string;
  role: UserRole;
  verify: number;

  phoneNumber?: string; // thêm phoneNumber
  dateOfBirth?: string; // hoặc Date tùy bạn
  bloodType?: string;
  address?: string;
  emergencyContact?: {
    name: string;
    phoneNumber: string;
  };
  medicalConditions?: string | string[];
}

// Define auth context interface
interface AuthContextType {
  user: User | null;
  login: (email: string, password: string) => Promise<boolean>;
  logout: () => Promise<void>;
  isAuthenticated: boolean;
  isLoading: boolean;
  error: string | null;

  requestPasswordReset: (email: string) => Promise<boolean>; // thêm đây
}

// Create auth context
const AuthContext = createContext<AuthContextType | undefined>(undefined);

// Auth provider component
export const AuthProvider: React.FC<{ children: ReactNode }> = ({
  children,
}) => {
  const [user, setUser] = useState<User | null>(null);
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [error, setError] = useState<string | null>(null);

  React.useEffect(() => {
    const token = localStorage.getItem("access_token");
    const userData = localStorage.getItem("user_data");

    if (token && userData) {
      try {
        const parsedUser = JSON.parse(userData);
        setUser(parsedUser);
      } catch (err) {
        console.error("Failed to parse stored user data:", err);
        localStorage.removeItem("access_token");
        localStorage.removeItem("refresh_token");
        localStorage.removeItem("user_data");
      }
    }
  }, []);

  const login = async (email: string, password: string): Promise<boolean> => {
    setIsLoading(true);
    setError(null);

    try {
      const response = await fetch("http://localhost:5000/auth/login", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
        credentials: "include",
        body: JSON.stringify({
          email: email.trim().toLowerCase(),
          password,
        }),
      });

      const data = await response.json();

      if (response.ok) {
        if (data.result?.access_token) {
          localStorage.setItem("access_token", data.result.access_token);
        }
        if (data.result?.refresh_token) {
          localStorage.setItem("refresh_token", data.result.refresh_token);
        }

        const userData: User = {
          _id: "temp_id",
          name: email.split("@")[0],
          email: email,
          role: data.result?.role ?? UserRole.User,
          verify: 1,
        };

        setUser(userData);
        localStorage.setItem("user_data", JSON.stringify(userData));

        if (data.redirectTo) {
          setTimeout(() => {
            window.location.href = data.redirectTo;
          }, 100);
        }

        return true;
      } else {
        let errorMessage = "Login failed. Please try again.";
        switch (response.status) {
          case 400:
          case 422:
            errorMessage = data.message || "Invalid email or password format";
            break;
          case 401:
            errorMessage = data.message || "Invalid email or password";
            break;
          case 404:
            errorMessage = "User not found";
            break;
          case 500:
            errorMessage = "Server error. Please try again later.";
            break;
          default:
            errorMessage = data.message || `Login failed (${response.status})`;
        }
        throw new Error(errorMessage);
      }
    } catch (err) {
      console.error("Login error:", err);
      let errorMessage = "Login failed. Please try again.";
      if (err instanceof Error) {
        if (err.message.includes("Failed to fetch")) {
          errorMessage =
            "Cannot connect to server. Please check if the backend is running on http://localhost:5000";
        } else if (
          err.message.includes("NetworkError") ||
          err.message.includes("network")
        ) {
          errorMessage =
            "Network error. Please check your internet connection.";
        } else {
          errorMessage = err.message;
        }
      }
      setError(errorMessage);
      return false;
    } finally {
      setIsLoading(false);
    }
  };

  const logout = async (): Promise<void> => {
    setIsLoading(true);
    try {
      const refreshToken = localStorage.getItem("refresh_token");
      const accessToken = localStorage.getItem("access_token");

      if (refreshToken && accessToken) {
        await fetch("http://localhost:5000/users/logout", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${accessToken}`,
          },
          body: JSON.stringify({ refresh_token: refreshToken }),
        });
      }
    } catch (err) {
      console.error("Logout error:", err);
    } finally {
      localStorage.removeItem("access_token");
      localStorage.removeItem("refresh_token");
      localStorage.removeItem("user_data");
      setUser(null);
      setError(null);
      setIsLoading(false);
      window.location.href = "/login";
    }
  };

  // **Hàm requestPasswordReset mới**
  const requestPasswordReset = async (email: string): Promise<boolean> => {
    setIsLoading(true);
    setError(null);
    try {
      const response = await fetch(
        "http://localhost:5000/auth/request-password-reset",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ email }),
        }
      );

      if (response.ok) {
        return true;
      } else {
        const data = await response.json();
        setError(data.message || "Failed to request password reset");
        return false;
      }
    } catch (err) {
      setError("Network error");
      return false;
    } finally {
      setIsLoading(false);
    }
  };

  // Add requestPasswordReset vào value
  const value = {
    user,
    login,
    logout,
    isAuthenticated: !!user,
    isLoading,
    error,
    requestPasswordReset, // *** ĐÂY ***
  };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
};
// Custom hook to use auth context
export const useAuth = (): AuthContextType => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error("useAuth must be used within an AuthProvider");
  }
  return context;
};

// Role-based route protection component
interface ProtectedRouteProps {
  children: ReactNode;
  allowedRoles: UserRole[];
}

export const ProtectedRoute: React.FC<ProtectedRouteProps> = ({
  children,
  allowedRoles,
}) => {
  const { user, isAuthenticated } = useAuth();

  if (!isAuthenticated) {
    window.location.href = "/login";
    return null;
  }

  if (user && !allowedRoles.includes(user.role)) {
    // Redirect to appropriate dashboard based on role
    switch (user.role) {
      case UserRole.User:
        window.location.href = "/member-dashboard";
        break;
      case UserRole.Staff:
        window.location.href = "/staff-dashboard";
        break;
      case UserRole.Admin:
        window.location.href = "/admin";
        break;
      default:
        window.location.href = "/";
        break;
    }
    return null;
  }

  return <>{children}</>;
};

// Helper function to get dashboard route based on user role
export const getDashboardByRole = (role: UserRole): string => {
  switch (role) {
    case UserRole.User:
      return "/member-dashboard";
    case UserRole.Staff:
      return "/staff-dashboard";
    case UserRole.Admin:
      return "/admin";
    default:
      return "/";
  }
};
